import {
    Component,
    OnInit,
    AfterViewInit,
    ElementRef,
    Input,
    Output,
    EventEmitter,
    HostBinding,
    ViewChild,
    TemplateRef,
    NgZone,
    ChangeDetectorRef,
    OnChanges,
    SimpleChanges,
    ViewEncapsulation,
    Optional,
} from "@angular/core";
import { NotifyService, NotifyOptions } from "@farris/ui-notify";
import { BsModalService } from "@farris/ui-modal";
import {
    FFileUploadAPIService,
    FFileUploaderService,
} from "../../service/index";
import { FFileSelectDirective } from "../../directive/index";
import { MultiDownloadInfoComponent } from "../multi-download-info/multi-download-info.component";
import {
    UploadOutput,
    UploadInput,
    UploadFile,
    UploaderOptions,
    FUploadFileExtend,
    DocMetaProperty,
    UploadStatus,
} from "../../model/index";
import { Observable, Subject } from "rxjs";
import { FFileUtils } from "../../utils/index";
import {
    trigger,
    style,
    state,
    animate,
    transition,
} from "@angular/animations";

import ResizeObserver from "resize-observer-polyfill";
import { LocaleService } from "@farris/ui-locale";
import { FARRIS_UPLOADFILE_LOCALEDATA } from "../../locale";

@Component({
    selector: "f-file-upload-and-preview",
    templateUrl: "./upload-and-preview.component.html",
    styleUrls: ["./upload-and-preview.component.css"],
    providers: [FFileUploaderService],
    animations: [
        trigger("hideUploaded", [
            state("show", style({ opacity: 1, height: "*" })),
            state("hide", style({ opacity: 0, height: 0 })),
            // transition('* => void', [
            //   style({ opacity: 1}),
            //   animate(200, style({ opacity: 0}))
            // ])
            transition("* => hide", animate("0.5s")),
        ]),
    ],
    encapsulation: ViewEncapsulation.None,
})
export class UploadAndPreviewComponent
    implements OnInit, AfterViewInit, OnChanges {
    @HostBinding("class.ffileUploadAndPreview") cls = true;
    @HostBinding("class.ffileUploadAndPreview-content-fill") get fillState() {
        return this.contentFill;
    }
    // 预览模板
    @ViewChild("previewFileNameTmpl") previewFileNameTmpl: TemplateRef<void>;
    // 文件大小模板
    @ViewChild("fileSizeTmpl") fileSizeTmpl: TemplateRef<void>;
    // 文件状态模板
    @ViewChild("uapStateTmpl") uapStateTmpl: TemplateRef<void>;
    // 预览操作模板
    @ViewChild("previewActionTmpl") previewActionTmpl: TemplateRef<void>;
    // 预览操作模板
    @ViewChild("previewDateTmpl") previewDateTmpl: TemplateRef<void>;
    // 上传操作模板
    @ViewChild("uploadFileNameTmpl") uploadFileNameTmpl: TemplateRef<void>;
    /**
     * 每个组可以设置特殊服务参数，来覆盖默认的统一模块级配置
     */
    @Input()
    set extendServerConfig(config: any) {
        if (config) {
            this.fileUploaderSer.setExtendServerConfig(config);
        }
    }
    // 内容区域填充
    @Input() contentFill = false;
    /** 启用手动调整展示顺序 */
    @Input() enableOrder = false;

    @Input() orderField = "order";

    private _renameFromDialog = "";
    // 记录列字段
    private _previewColumnsFields = [];
    // 预置列信息
    private _presetPreviewColumns = [
        { field: "name", width: 200, title: "文件名", checkbox: true },
        { field: "size", width: 100, title: "大小" },
        { field: "createTime", width: 100, title: "日期" },
        { field: "state", width: 100, title: "状态" },
        { field: "action", width: 100, title: "操作" },
    ];
    // 内部记录列信息
    private _previewColumns = [];
    @Input()
    set previewColumns(value) {
        if (value && value.length > 0) {
            this._previewColumns = [...value];
        } else {
            this._presetPreviewColumns = [...this._presetPreviewColumns];
        }
        this.getFieldsFromColumns();
    }
    get previewColumns() {
        return this._previewColumns;
    }
    // 格式化预览数据
    private _fileInfos: FUploadFileExtend[] = [];
    @Input()
    set fileInfos(val: FUploadFileExtend[]) {
        this._fileInfos = this.formatPreviewDatas(val);
        this._renameFromDialog = "";
    }
    get fileInfos(): FUploadFileExtend[] {
        return this._fileInfos;
    }
    /**
     * 附件预览配置---------------------------------------开始
     */
    @Input() previewDateFormat = "yyyy-MM-dd";
    // 只读的状态，控制附件是否可删除
    // 默认处于查看状态
    @Input() previewReadonly = true;
    @Input() previewVisible = true;
    // 是否启用批量操作
    @Input() previewEnableMulti = true;
    // 预览显示类别
    @Input() previewShowType = "list";
    // 启用重命名
    @Input() previewEnableRename = true;
    // 默认重命名
    @Input() previewDefaultRename = "";
    // 附件预览，预览事件
    @Output() filePreviewEvent = new EventEmitter<FUploadFileExtend>();
    // 附件预览，下载事件，抛出对象给第三方处理
    @Output() fileDownloadEvent = new EventEmitter<{
        fileInfos: FUploadFileExtend[];
        name: string;
    }>();
    // 附件预览，多选事件
    @Output() fPreviewMultiSelectedEvent = new EventEmitter<
        FUploadFileExtend[]
    >();
    // 附件预览，删除事件，抛出对象给第三方处理
    @Output() fileRemoveEvent = new EventEmitter<FUploadFileExtend[]>();
    // 全选状态
    previewSelectAllBtnChecked = false;
    // 记录选中数据
    previewMultiSelected: FUploadFileExtend[] = [];
    // 构造预览的配置数据
    get previewSettings() {
        return {
            readonly: this.previewReadonly,
            visible: this.previewVisible,
            enableMulti: this.previewEnableMulti,
            enableRename: this.previewEnableRename,
        };
    }
    /**
     * 附件预览配置---------------------------------------结束
     */

    /**
     * 附件上传配置---------------------------------------开始
     */
    // 附件上传选择指令
    @ViewChild(FFileSelectDirective) fileUpload: FFileSelectDirective;
    // 附件上传部分禁用状态
    @Input() uploadDisabled = false;
    private tUploadVisible = true;

    // 附件上传部分是否可见
    @Input()
    set uploadVisible(value) {
        this.tUploadVisible = value;
        if (!value) {
            this.uploadReset();
        }
    }
    get uploadVisible() {
        return this.tUploadVisible;
    }
    // 需要重置,通过不断的赋值来改变
    @Input() uploadNeedReset: Observable<any> = new Subject();
    // 启用选择文件按钮
    @Input() uploadEnableSelect = true;
    // 选择文件按钮上传问题
    @Input() uploadSelectText = '';
    // 启用多选
    @Input() uploadEnableMulti = true;
    // 已经上传文件
    @Input() uploadedCount = 0;
    // 公共的扩展数据
    @Input() extendDatas: {
        [key: string]: any;
        extProperty?: DocMetaProperty | null; //
    };

    /** 整个控件禁用 */
    @Input() disabled = false;
    /** 禁止下载 */
    @Input() noDownload = false;

    @Input()
    set uploadOptions(uploadOpts: UploaderOptions) {
        if (uploadOpts) {
            if (uploadOpts.hasOwnProperty("allowedContentTypes")) {
                this.allowedContentTypes = uploadOpts.allowedContentTypes.join(",");
                this.allowedContentTypes =
                    this.allowedContentTypes == "" ? "*" : this.allowedContentTypes;
            }
            if (uploadOpts.hasOwnProperty("maxUploads")) {
                this.maxUploads = uploadOpts.maxUploads;
            }
            if (uploadOpts.hasOwnProperty("maxFileSize")) {
                this.maxFileSize = FFileUtils.humanizeBytes(
                    1024 * 1024 * uploadOpts.maxFileSize
                );
            }
            this.selfUploadOpts = Object.assign(this.selfUploadOpts, uploadOpts);
        }
    }
    get uploadOptions() {
        return this.selfUploadOpts;
    }
    // 附件上传中，上传完成抛出的事件
    @Output() fUploadDoneEvent = new EventEmitter<FUploadFileExtend[]>();
    // 附件上传中，删除附件事件
    @Output() fUploadRemovedEvent = new EventEmitter<FUploadFileExtend[]>();

    /** 文件放下事件 */
    @Output() afterOrdered = new EventEmitter();

    @ViewChild("uaptb") uploadAndPreviewTbodyRef: ElementRef<any>;

    private uploadAndPreviewTableHeader: ElementRef = null;
    private uploadAndPreviewTableHeaderCreated$ = new Subject();
    @ViewChild("uapth") set uploadAndPreviewTheadRef(th: ElementRef) {
        if (th) {
            this.uploadAndPreviewTableHeader = th;
            this.setColumnWidth();
            this.ro.observe(th.nativeElement);
            this.uploadAndPreviewTableHeaderCreated$.next(th);
        } else {
            this.uploadAndPreviewTableHeader = null;
            if (this.ro) {
                this.ro.disconnect();
            }
            this.uploadAndPreviewTableHeaderCreated$.next(null);
        }
    }
    get uploadSettings() {
        return {
            disabled: this.uploadDisabled,
            visible: this.uploadVisible,
            enableMulti: this.uploadEnableMulti,
            extendDatas: this.extendDatas,
        };
    }
    // 允许上传的文件类型
    allowedContentTypes = "*";
    // 默认不限制附件上传个数
    maxUploads = 0;
    // 单位KB，默认是12M
    maxFileSize = "12MB";
    // 记录文件个数
    uploadFiles: UploadOutput[];
    // 标记通过选择按钮处理的事件
    uploadSelectInput: EventEmitter<UploadInput>;
    private selfUploadOpts: UploaderOptions = {};

    private ro: ResizeObserver | null = null;

    private localSer: LocaleService = null;
    /**
     * 附件上传配置---------------------------------------结束
     */

    /**
     *
     * @param el
     * @param uploadAPISer
     * @param fileUploaderSer
     * @param notifyService
     * @param ngZone
     */
    constructor(
        private el: ElementRef,
        private uploadAPISer: FFileUploadAPIService,
        private fileUploaderSer: FFileUploaderService,
        private notifyService: NotifyService,
        private ngZone: NgZone,
        private modalSer: BsModalService,
        private cd: ChangeDetectorRef
    ) {
        this.uploadFiles = [];
        this.uploadSelectInput = new EventEmitter<UploadInput>();
        // 重置----------可能有问题
        this.uploadNeedReset.subscribe((data) => {
            this.uploadReset();
        });

        this.ro = new ResizeObserver((e) => {
            this.setColumnWidth();
        });

        this.localSer = this.notifyService.localeService;
        this.localSer.setLocaleData(FARRIS_UPLOADFILE_LOCALEDATA);
    }

    ngOnInit() {
        if (this._previewColumns.length == 0) {
            this.previewColumns = [...this._presetPreviewColumns];
        }

        if (!this.uploadSelectText) {
            this.uploadSelectText = this.localSer.getValue('uploadAndPreview.uploadSelectText') || '上传附件';
        }
    }
    ngAfterViewInit() { }

    ngOnChanges(changes: SimpleChanges): void {
        const {
            previewReadonly,
            previewVisible,
            previewEnableMulti,
            previewDefaultRename,
        } = changes;
        if (previewReadonly || previewVisible || previewEnableMulti) {
            // 重置附件预览
            this.previewMultiSelected = [];
            this.fileInfos.forEach((item) => {
                item.checked = false;
            });
            this.previewSelectAllBtnChecked = false;
            this._renameFromDialog = "";
        }
        if (previewDefaultRename) {
            // 重置预览重命名记录
            this._renameFromDialog = "";
        }
    }
    /**
     * 选择或取消所有
     * @param ev
     */
    selectOrCancelAllHandler(ev) {
        if (ev["checked"]) {
            // 全选
            this.fileInfos.forEach((item) => {
                item.checked = true;
            });
            this.previewMultiSelected = [...this.fileInfos];
            this.previewSelectAllBtnChecked = true;
        } else {
            // 取消
            this.previewMultiSelected = [];
            this.fileInfos.forEach((item) => {
                item.checked = false;
            });
            this.previewSelectAllBtnChecked = false;
        }
    }
    /**
     * 格式化文件大小
     * @param bytes
     */
    formateBytes(bytes) {
        return FFileUtils.humanizeBytes(bytes);
    }
    /**
     * 获取文档类型对应的class
     * @param fileName
     */
    getFileTypeClassName(fileName) {
        let clsName = "ffilepreview--filetype";
        // 如果名称为空
        if (!fileName) {
            return clsName + "-any";
        }
        let lastDotIndex = fileName.lastIndexOf(".");
        let fileSuffix = "";
        if (lastDotIndex > -1) {
            fileSuffix = fileName.substring(lastDotIndex + 1).toLocaleLowerCase();
        }
        switch (fileSuffix) {
            case "pdf":
                clsName += "-pdf";
                break;
            case "jpeg":
            case "jpg":
            case "gif":
            case "svg":
            case "png":
            case "bmp":
                clsName += "-img";
                break;
            case "ppt":
            case "pptx":
                clsName += "-ppt";
                break;
            case "doc":
            case "docx":
                clsName += "-doc";
                break;
            case "xls":
            case "xlsx":
                clsName += "-xls";
                break;
            case "txt":
                clsName += "-txt";
                break;
            case "zip":
                clsName += "-zip";
                break;
            default:
                clsName += "-any";
        }
        return clsName;
    }
    /**
     * 格式化预览数据
     * @param val
     */
    private formatPreviewDatas(val) {
        if (!val.length || this.previewMultiSelected.length !== val.length) {
            this.previewSelectAllBtnChecked = false;
        }


        let formatValue = val.map((item: FUploadFileExtend) => {
            if (!item.hasOwnProperty("size")) {
                item["size"] = undefined;
            }
            if (!item.hasOwnProperty("createTime")) {
                item["createTime"] = undefined;
            }

            return item;
        });

        if (this.enableOrder && this.orderField) {
            formatValue.sort(this.sortPreviewData(this.orderField));
        } else {
            formatValue.sort(this.sortPreviewData("createTime"));
        }
        if (this.previewEnableMulti) {
            let newFormatValue = formatValue.map((item: FUploadFileExtend) => {
                item["checked"] = !!this.previewMultiSelected.find(n => n.id === item.id) || this.previewSelectAllBtnChecked;
                return item;
            });
            // 归集已选数据
            if (this.previewSelectAllBtnChecked) {
                this.previewMultiSelected = [...newFormatValue].filter(n => n.checked);
            } else {
                this.previewMultiSelected = [];
            }
            // 返回格式化数据
            return newFormatValue;
        } else {
            return formatValue;
        }
    }


    private getValue(field: string, data: any) {
        if (!data) {
            return "";
        }
        let resultVal = "";
        if (field.indexOf(".") === -1 && data.hasOwnProperty(field)) {
            resultVal = data[field];
        } else {
            resultVal = field.split(".").reduce((obj, key) => {
                if (obj) {
                    return obj[key];
                } else {
                    return null;
                }
            }, data);
        }
        return resultVal;
    }

    private sortPreviewData(key) {
        const _this = this;
        return (x, y) => {
            if (!x[key]) {
                return 1;
            }
            if (!y[key]) {
                return -1;
            }

            if (_this['orderField'] && key === _this['orderField']) {
                let a = "";
                let b = "";
                if (key.indexOf(".") > -1) {
                    a = _this.getValue(key, x);
                    b = _this.getValue(key, y);
                } else {
                    a = x[key];
                    b = y[key];
                }
                return a > b ? 1 : -1;
            } else {
                var oDate1 = new Date(x[key]);
                var oDate2 = new Date(y[key]);
                return oDate1.getTime() > oDate2.getTime() ? -1 : 1;
            }
        };
    };

    /**
     * 格式化处理数据
     */
    private getFieldsFromColumns() {
        this._previewColumnsFields = this._previewColumns.map(
            (item) => item["field"]
        );
    }
    /**
     * 根据列获取对应的列模板
     * @param columnInfo
     */
    getColumnTemplate(columnInfo, type = "preview") {
        let result;
        if (columnInfo.hasOwnProperty("template") && columnInfo["template"]) {
            return columnInfo["template"];
        }
        if (type == "preview") {
            switch (columnInfo["field"]) {
                case "state":
                    result = this["uapStateTmpl"];
                    break;
                case "name":
                    result = this["previewFileNameTmpl"];
                    break;
                case "action":
                    result = this["previewActionTmpl"];
                    break;
                case "size":
                    result = this["fileSizeTmpl"];
                    break;
                case "createTime":
                    result = this["previewDateTmpl"];
                    break;
                default:
                    result = "";
            }
        } else {
            switch (columnInfo["field"]) {
                case "state":
                    result = this["uapStateTmpl"];
                    break;
                case "name":
                    result = this["uploadFileNameTmpl"];
                    break;
                case "size":
                    result = this["fileSizeTmpl"];
                    break;
                default:
                    result = "";
            }
        }
        return result;
    }
    /**
     * 多选变更
     */
    previewMultiSelectChange(ev) {
        let hasInArray = this.previewMultiSelected.findIndex(
            (item) => item["id"] == ev["id"]
        );
        let findInArray = this.fileInfos.findIndex(
            (item) => item["id"] == ev["id"]
        );
        if (findInArray > -1) {
            this.fileInfos[findInArray]["checked"] = ev["checked"];
        }

        if (ev["checked"]) {
            //选入
            if (hasInArray == -1) {
                if (findInArray > -1) {
                    this.previewMultiSelected.push(this.fileInfos[findInArray]);
                }
            }
        } else {
            //移出
            if (hasInArray > -1) {
                this.previewMultiSelected.splice(hasInArray, 1);
            }
        }
        // 判断是否全选
        if (this.fileInfos.length > 0) {
            if (this.previewMultiSelected.length == this.fileInfos.length) {
                //都全选
                this.previewSelectAllBtnChecked = true;
            } else {
                // 未全选
                this.previewSelectAllBtnChecked = false;
            }
        }
        // 抛出多选
        this.fPreviewMultiSelectedEvent.emit(this.previewMultiSelected);
    }
    /**处理附件上传---------------------------------------------------------开始-- */
    /**
     * 统一触发 附件选择和拖拽附件的事件
     * @param ev
     */
    private inputUploadEventEmit(ev: UploadInput) {
        // 如果文件上传启用
        if (this.uploadEnableSelect) {
            this.uploadSelectInput.emit(ev);
        }
        // this.uploadDragInput.emit(ev);
    }
    /**单个文件上传 */
    startUpload(file: UploadFile): void {
        /**
         * Todo:后续可以从上传区块上取内容进行扩展
         */
        const event: UploadInput = this.uploadAPISer.getAssignAPI(
            "multipartUpload",
            {
                file: file,
                type: "multipartUpload",
                data: this.extendDatas,
            }
        );
        this.inputUploadEventEmit(event);
    }
    // 重置方法
    uploadReset() {
        this.uploadFiles = [];
        // 如果文件上传启用
        if (this.uploadEnableSelect) {
            this.fileUpload && this.fileUpload.reset();
        }
    }

    /**
     * 触发绑定
     */
    triggerFileSelect(ev) {
        ev && ev.stopPropagation();
        // 禁用时
        if (this.uploadDisabled) {
            return false;
        }
        this.fileUpload.elementRef.nativeElement.click();
    }
    /**
     * 选择附件按钮，处理抛出事件
     * @param output
     */
    selectUploadOutput(output: UploadOutput): void {
        this.uploadOutputHandler(output);
    }
    /**
     * allAddedToQueue 选中文件追加到队列
     * @param output
     */
    private uploadOutputHandler(output: UploadOutput): void {
        if (output.type === "addedToQueue" && typeof output.file !== "undefined") {
            this.uploadFiles.push({ type: output.type, file: output.file });
            // 开始上传
            this.startUpload(output.file);
        } else if (output.type === "start") {
            // 开始上传
            this.updateFilesState(output, "");
        } else if (output.type == "done") {
            // 上传完成
            this.updateFilesState(output, "done");
            this.fUploadDoneEvent.emit(
                FFileUtils.changeUploadFileToFileExtend(output.files)
            );
            // 上传完成后不在当前页面上显示
            this.hideAfterLoaded(output);
        } else if (
            output.type === "cancelled" ||
            output.type === "removed" ||
            output.type == "error"
        ) {
            // 删除或者移除文件
            this.uploadFiles = this.uploadFiles.filter((fileOutput: UploadOutput) => {
                return (
                    output.files.findIndex((item) => fileOutput.file.id == item.id) < 0
                );
            });
            // 给出提示
            this.showNotify(output);
        } else if (
            output.type === "rejected" &&
            typeof output.file !== "undefined"
        ) {
            // 上传被拒绝
            this.showNotify(output);
        }
    }
    /**
     * 临时删除附件
     * @param output
     */
    private hideAfterLoaded(output: UploadOutput): void {
        // 删除或者移除文件
        let ids = output.files.map((fileItem) => fileItem.id);
        this.uploadFiles = this.uploadFiles.filter((fileOutput: UploadOutput) => {
            let tIndex = ids.findIndex((tId) => tId == fileOutput.file.id);
            return tIndex > -1 ? false : true;
        });
        const event: UploadInput = this.uploadAPISer.getAssignAPI("hide", {
            type: "hide",
            id: ids.join(","),
        });
        this.inputUploadEventEmit(event);
    }
    /**
     * 处理上传
     * @param output
     */
    private showNotify(output) {
        let notifyType = "warning";
        if (output.type == "cancelled" || output.type == "removed") {
            notifyType = "success";
        }
        let message = output.hasOwnProperty("message") ? output["message"] : "";
        this.notifyService[notifyType](<NotifyOptions>{
            msg: message,
            timeout: 3000,
        });
    }
    /**
     * 更新附件的状态
     * @param output
     * @param eventType
     */
    private updateFilesState(output: UploadOutput, eventType) {
        let startFiles = [];
        if (output.hasOwnProperty("files") && output.files.length > 0) {
            // 此处更新界面可能有性能问题
            startFiles = output.files;
        } else if (typeof output.file !== "undefined") {
            startFiles = [output.file];
        }
        eventType = eventType ? eventType : output.type;
        startFiles.map((item) => {
            const index = this.uploadFiles.findIndex(
                (fileOutput) =>
                    typeof item !== "undefined" && fileOutput.file.id === item.id
            );
            // 变更状态
            if (index > -1) {
                this.uploadFiles[index] = { type: eventType, file: item };
            }
        });
    }
    /**
     * 是否判断有类型限制
     */
    judgeContentTypeLimit() {
        let tTypes = this.allowedContentTypes.split(",");
        let hasAllIndex = tTypes.findIndex((item) => {
            return item == "*";
        });
        return hasAllIndex == -1;
    }
    /**
     * 判断是否有文件大小的限制
     */
    judegeFileSizeLimit() {
        return this.maxFileSize != "0 Byte";
    }
    /**
     * 获取上传进度
     */
    getUploadProgress(uploadInfo: UploadOutput) {
        if (uploadInfo["type"] == "done") {
            return 100;
        }
        if (uploadInfo["file"]["progress"] && uploadInfo["file"]["progress"]) {
            if (uploadInfo["file"]["progress"]["status"] == UploadStatus.Uploading) {
                let tPercentage = uploadInfo["file"]["progress"]["data"]["percentage"];
                return tPercentage > 5 ? tPercentage : 5;
            }
            return 5;
        }
        return 5;
    }
    /**
     * 处理显示
     * @param ev
     * @param fileInfo
     */
    getUploadFileSizeProgress(uploadInfo: UploadOutput) {
        if (!uploadInfo) {
            return "";
        }
        if (uploadInfo["type"] == "done") {
            return FFileUtils.humanizeBytes(uploadInfo.file.size);
        }
        let tPercentage = 5;
        if (uploadInfo["file"]["progress"] && uploadInfo["file"]["progress"]) {
            if (uploadInfo["file"]["progress"]["status"] == UploadStatus.Uploading) {
                tPercentage = uploadInfo["file"]["progress"]["data"]["percentage"];
                tPercentage = tPercentage > 5 ? tPercentage : 5;
            }
        }
        return (
            FFileUtils.humanizeBytes((uploadInfo.file.size * tPercentage) / 100) +
            "/" +
            FFileUtils.humanizeBytes(uploadInfo.file.size)
        );
    }

    /**处理附件上传---------------------------------------------------------结束-- */

    /**
     * 预览
     * @param ev
     * @param fileInfo
     */
    filePreviewHandler(ev: Event, fileInfo: FUploadFileExtend) {
        ev.stopImmediatePropagation();
        fileInfo.showDownload = !this.noDownload;
        this.filePreviewEvent.emit(fileInfo);
    }
    /**
     * 下载
     * @param ev
     * @param fileInfo
     */
    fileDownloadHandler(ev: Event, fileInfo: FUploadFileExtend) {
        ev.stopImmediatePropagation();
        this.fileDownloadEvent.emit({ fileInfos: [fileInfo], name: "" });
    }
    /**
     * 删除数据
     * @param ev
     * @param fileInfo
     */
    fileRemoveHandler(ev: Event, fileInfo: FUploadFileExtend) {
        ev.stopImmediatePropagation();
        if (fileInfo.checked) {
            this.previewMultiSelected = this.previewMultiSelected.filter(n => n.id !== fileInfo.id);
        }
        this.fileRemoveEvent.emit([fileInfo]);
    }
    /**
     * 批量删除
     * @param ev
     */
    multiFileRemoveHandler(ev: Event) {
        ev.stopImmediatePropagation();
        this.fileRemoveEvent.emit(this.previewMultiSelected);
    }
    fileMultiDownloadHandler(ev: Event) {
        ev.stopImmediatePropagation();
        let rename = "";
        if (this.previewMultiSelected.length == 1) {
            this.fileDownloadEvent.emit({
                fileInfos: this.previewMultiSelected,
                name: rename,
            });
            return;
        }
        // 启用了重命名
        if (this.previewEnableRename) {
            let self = this;
            const dlgRef = this.modalSer.show(MultiDownloadInfoComponent, {
                width: 440,
                height: 200,
                showMaxButton: false,
                title: "附件下载",
                initialState: {
                    fileTypeCls: this.getFileTypeClassName(self.previewMultiSelected[0].name),
                    downloadInfos: self.previewMultiSelected,
                    downloadName:
                        self._renameFromDialog && !self.previewDefaultRename
                            ? self._renameFromDialog
                            : self.previewDefaultRename,
                },
                showButtons: true,
                buttons: [
                    {
                        text: "取消",
                        cls: "btn btn-secondary",
                        handle: () => {
                            dlgRef.close();
                        },
                    },
                    {
                        text: "确定",
                        cls: "btn btn-primary",
                        handle: () => {
                            const contentRef = dlgRef.content as MultiDownloadInfoComponent;
                            rename = contentRef.getDownloadName();
                            self.fileDownloadEvent.emit({
                                fileInfos: self.previewMultiSelected,
                                name: rename,
                            });
                            dlgRef.close();
                        },
                    },
                ],
            });
        } else {
            //未启用重命名
            rename = this.getName(this.previewMultiSelected[0]["name"]);
            this.fileDownloadEvent.emit({
                fileInfos: this.previewMultiSelected,
                name: rename,
            });
        }
    }
    private getName(name: string) {
        if (name.lastIndexOf(".") > 0) {
            return name.substring(0, name.lastIndexOf("."));
        }
        return "";
    }
    showPreviewStateColumn(columnInfo) {
        if (
            columnInfo["field"] == "state" &&
            (this.uploadDisabled || !this.uploadVisible || this.noDownload)
        ) {
            return false;
        }
        return true;
    }

    /** 拖拽排序后事件 */
    onOrderFileDrop($event) {
        const { removedIndex, addedIndex, payload } = $event;

        const removedItem = this.fileInfos.splice(removedIndex, 1);
        this.fileInfos.splice(addedIndex, 0, ...removedItem);

        this.cd.detectChanges();

        const newOrderItems = this.fileInfos.map((n) => n.id);
        this.afterOrdered.emit({ data: newOrderItems });
    }

    private setColumnWidth() {
        if (this.uploadAndPreviewTableHeader && this.enableOrder) {
            const tableWidth = this.uploadAndPreviewTableHeader.nativeElement.offsetWidth;
            const cols = this.uploadAndPreviewTableHeader.nativeElement.querySelectorAll("th");
            const trs = this.uploadAndPreviewTbodyRef.nativeElement.querySelectorAll("tr");

            this.uploadAndPreviewTbodyRef.nativeElement.style.flexDirection = "column";
            this.uploadAndPreviewTbodyRef.nativeElement.style.display = "flex";
            this.uploadAndPreviewTbodyRef.nativeElement.style.width = tableWidth + "px";

            Array.from(trs).forEach((tr: any) => {
                tr.style.width = tableWidth + "px";
                const tds = tr.querySelectorAll("td");
                Array.from(cols).forEach((th: any, index: number) => {
                    tds[index].style.width = th.offsetWidth + "px";

                    if(index === 0) {
                        tds[index].querySelector('.uploadAndpreview--title-container').style.width = (th.offsetWidth - 56) + 'px';
                    }
                });
            });
        }
    }
}
