import { Subscription, Observable } from 'rxjs';

export interface UploaderOptions {
    concurrency?: number;
    allowedContentTypes?: string[];
    maxUploads?: number;
    maxFileSize?: number;//MB为单位
    uploadedCount?: number;
}

export interface BlobFile extends Blob {
    name: string;
}

export enum UploadStatus {
    Queue,
    Uploading,
    Done,
    Cancelled,
    Remove,// 正在删除
    Error
}

export interface UploadProgress {
    status: UploadStatus;
    data?: {
        percentage: number;
        speed?: number;
        speedHuman?: string;
        startTime?: number | null;
        endTime?: number | null;
        eta?: number | null;
        etaHuman?: string | null;
    };
}
export interface FUploadFileExtend {
    id: string;
    name: string;
    disabled?: boolean;
    checked?: boolean;
    size?: number | undefined;//文件大小
    createTime?: string | undefined;//创建日期
    type?: string;
    extend?: any | null;//记录返回数据
    extendStatus?: number;
    extendHeaders?: { [key: string]: string } | null;
    showDownload?: boolean;
}

export interface UploadFile {
    id: string;
    fileIndex: number;
    lastModifiedDate: Date;
    name: string;
    size: number;
    type: string;
    form: FormData;
    progress: UploadProgress;//
    response?: any;//记录返回数据
    responseStatus?: number;
    sub?: Subscription | any;
    nativeFile?: File;
    responseHeaders?: { [key: string]: string };
}


export interface UploadOutput {
    type: 'addedToQueue' | 'allAddedToQueue' | 'uploading' | 'done' | 'start' | 'cancelled' | 'dragOver'
    | 'dragOut' | 'drop' | 'removed' | 'allRemoved' | 'rejected' | 'allDone' | 'allStart' | 'allCancelled' | 'error';
    file?: UploadFile;
    nativeFile?: File;
    message?: String;
    files?: UploadFile[];
}
export interface UploadConfig {
    url?: string;
    method?: string;// POST，GET 
    id?: string;
    fieldName?: string;
    fileIndex?: number;
    file?: UploadFile;
    data?: {
        [key: string]: any,
        extProperty?: DocMetaProperty | null //
    };
    headers?: { [key: string]: string } | null;
    includeWebKitFormBoundary?: boolean; // If false, only the file is send trough xhr.send (WebKitFormBoundary is omit)
    withCredentials?: boolean;
    timeout?: number;// 暂不支持
}

export interface UploadInput extends UploadConfig {
    type: 'upload' | 'uploadAll' | 'cancel' | 'cancelAll' | 'remove' | 'removeAll' | 'config' | 'hide' | 'multipartUpload' | 'sliceUpload';
    /** 分块上传时，每块大小默认为 1M */
    chunkSize?: number
}


export abstract class UploadServerService {
    constructor() {

    }
    abstract upload(files: UploadFile[], event: UploadInput, extendService: any): Observable<any>;
    abstract remove(files: UploadFile[], event: UploadInput, extendService: any): Observable<any>;
    abstract multipartUpload(file: UploadFile, event: UploadInput, extendService: any): Observable<any>;
}

export interface DocMetaProperty {
    extProperties1?: string;
    extProperties2?: string;
    extProperties3?: string;
    extProperties4?: string;
    extProperties5?: string;
}